import React, {useContext, useState} from "react";
import {observer} from "mobx-react-lite";
import {ProductType} from "../../store/store";
import shortid from 'shortid';
import {Context} from "../../index";
import s from "./ProductForm.module.css"
import {Button} from "@mui/material";

const ProductForm: React.FC = observer(() => {
    const {store} = useContext(Context)
    //      
    const [productName, setProductName] = useState("");
    const [productInfo, setProductInfo] = useState("");
    const [nameOKRB, setNameOKRB] = useState("");
    const [codeOKRB, setCodeOKRB] = useState("");
    const [count, setCount] = useState("");
    const [unitName, setUnitName] = useState("");
    const [error, setError] = useState<string | null>(null);

    //   
    const handleAddProduct = () => {
        const normalizedValue = count.replace(',', '.');
        if (isNaN(parseFloat(normalizedValue))) {
            alert(',    !')
        } else {
            if (productName && unitName) {
                const id = shortid.generate();
                const newProduct = {
                    productId: id,
                    productName,
                    count: parseFloat(normalizedValue),
                    unitName,
                    productInfo,
                    nameOKRB,
                    codeOKRB,
                };
                store.setProductList([...store.productList, newProduct]); //    
                setProductName(""); //    
                setProductInfo("");
                setCount("");
                setUnitName("");
                setNameOKRB("");
                setCodeOKRB("");
            } else {
                alert(",   ."); //  
            }
        }
    };

    return (
        <div className={s.formWrapper}>
            <h4>  </h4>
            <form
                onSubmit={(e) => {
                    e.preventDefault();
                    handleAddProduct();
                }}
            >
                <div className={s.inputBox}>
                    <label> :</label>
                    <input
                        type="text"
                        value={productName}
                        onChange={(e) => setProductName(e.target.value)}
                        required
                    />
                </div>
                <div className={s.inputBox}>
                    <label>:</label>
                    <input
                        type="text"
                        value={count}
                        onChange={(e) => setCount(e.target.value)}
                        placeholder=" "
                        required
                    />
                </div>
                <div className={s.inputBox}>
                    <label> :</label>
                    <input
                        type="string"
                        value={unitName}
                        onChange={(e) => setUnitName(e.target.value)}
                        required
                    />
                </div>
                <div className={s.inputBox}>
                    <label>  :</label>
                    <textarea
                        value={productInfo}
                        onChange={(e) => setProductInfo(e.target.value)}

                    />
                </div>
                <div className={s.inputBox}>
                    <label> :</label>
                    <input
                        type="text"
                        value={codeOKRB}
                        onChange={(e) => setCodeOKRB(e.target.value)}
                    />
                </div>
                <Button variant="contained" type="submit"> </Button>
            </form>
        </div>
    );
});

export default ProductForm;